export const pageQuery = graphql` query($uid: String){
  prismicBlogpost(uid: { eq: $uid }) {
    uid
    data {
      website_main_logo {
        alt
        url
      }
      name {
          html
          text
        }
        website_background_image {
          alt
          url
        }
        footer_line_image {
          alt
          url
        }
        footer_image {
          alt
          url
        }
        footer_image_url {
          url
        }
        logo_description {
          text
        }
        body {
          ... on PrismicBlogpostBodyPdfslice {
            primary {
              pdf_image{
                url
              }
            }
          }
        }
    }
  }
}`;
